<template>
  <div class="cart-add">
    <div class="cart-good-mes"><i class="cart-close" @click="close"></i>
      <div class="cart-good-img"><img :src="spImg"></div>
      <div class="cart-good-text">
        <div>￥{{selectOptions.price}}</div>
        <div>库存{{selectOptions.stock}}件</div>
        <div class="toSelect">请选择 <span v-for="(item,key) in attrNames">{{item}}</span></div>
      </div>
    </div>
    <div><div class="cart-list" v-for="(item,key) in attrs">
        <div class="title">{{item.attrName}}</div>
        <div class="conetnt"><span v-for="(item1,key1) in item.options" :class="{'selected':selectAttr[item.attrId] === item1.optionId}" @click="select(item.attrId,item1)">{{item1.optionName}}</span></div>
      </div>
      <div class="cart-list"><span>购买数量</span>
        <div class="cart-size-oper"><i class="shift" :class="{no:size<=0}" @click="shift"></i><i class="size">{{size}}</i><i class="push" @click="push"></i></div>
      </div></div>
    <footer @click="ok">确定</footer>
  </div>
</template>

<script>
  import {getAttrBySpuId, addCart, getAttrOption} from '../../service/getData'
  export default {
    props: ['oper', 'spuId', 'sellPrice', 'sellAmount','skuId'],
    data () {
      return {
        good: {},
        selectAttr: {},
        spImg: '/',
        size: 0,
        attrs: [],
        attrNames: [],
        attrOptions: [],
        selectOptions: {}
      }
    },
    methods: {
      ok: function () {
        let keys = Object.keys(this.selectAttr)
        if (keys.length === 0 || keys.length !== this.attrNames.length) {
          this.$toast({
            message: '请选择属性',
            duration: 1000
          })
          return
        }
        
        if (this.size <= 0) {
          this.$toast({
            message: '请选择你需要购买的数量!',
            duration: 1000
          })
          return
        }
        if (this.oper === 1) {
          this.$router.push({
            name: 'submit'
          })
        }
        this.$emit('closePop', false)
        if (this.oper === 0) {
          /* 请求后台 */
         let _this = this
          addCart(this.selectOptions.skuId, this.size)
          .then(res => {
            if (res.data.code === 1) {
              _this.size = 0
              _this.selectAttr = []
              _this.$toast('成功加入购物车!')
            }
          })
          .catch(() => {
            _this.$toast('加载失败')
          })
        }
      },
      close: function () {
        this.$emit('closePop', false)
      },
      shift: function () {
        if (this.size > 0) {
          this.size--
        }
      },
      push: function () {
        this.size++
      },
      select: function (attrId, obj) {
        let values, keys, key1 = 0, key2 = 0, size = 0
        this.$set(this.selectAttr, attrId, obj.optionId)
        keys = Object.keys(this.attrOptions)
        values = Object.values(this.selectAttr)
        /* 判断组合  */
       for (key1 in keys) {
         size =0
         for(key2 in values){
           if(keys[key1].indexOf(values[key2]) >= 0){
             size++
           }
         }
         if (size === this.attrNames.length) {
           this.selectOptions = this.attrOptions[keys[key1]]
           return
         }
       }
        console.log(this.selectOptions)
      }
    },
    created: function () {
    },
    mounted: function () {
    },
    watch: {
      oper: function () {
        this.materialActive = ''
        this.comboTypeActive = ''
        this.size = 0
      },
      spspuId: function () {
        var _this = this
        let key = 0
        getAttrBySpuId(this.spuId)
        .then(res => {
          _this.attrs = res.data
          for (key in res.data) {
            _this.$set(_this.attrNames, _this.attrNames.length, res.data[key].attrName)
          }
        })
        .catch(() => {
          _this.$toast('加载失败')
        })
        getAttrOption(this.spuId)
        .then(res => {
          _this.attrOptions = res.data
        })
        .catch(() => {
          _this.$toast('加载失败')
        })
      }
    },
    computed: {
      spspuId: function () {
        return this.spuId
      },
      spsellPrice: function () {
        return this.sellPrice
      },
      spsellAmount: function () {
        if (this.sellAmount === undefined) {
          return 0
        } else {
          return this.sellAmount
        }
      },
      spskuId: function () {
        return skuId
      }
    }
  }
</script>

<style>
/* 公用购物车添加*/
.sp-list{
  width: 100%;
}
.cart-add{
  position: relative;
  height: 5.15rem;
  width: 100%;
  padding:.1rem;
}
.cart-good-mes{
  height:1.105rem ;
  position: relative;
  border-bottom: .01rem solid #E5E5E5;
}
.cart-good-img{
    display: inline-block;
  width: 1.3rem;
  height: 100%;
  padding: 0 .1rem;
  transform: translateY(-.3rem);
}
.cart-good-img img{
  border-radius: .1rem;
  background-color: gainsboro;
}
.cart-good-text{
  display: inline-block;
  height: 100%;
  padding: .2rem .1rem;
  vertical-align: top;
  color: #333;
  font-size: .12rem;
}
.cart-good-text div{
  margin: .02rem;
}
.cart-good-text div:first-child{
  color: rgb(255, 161, 38);
}
.cart-close{
  position: absolute;
  right: .1rem;
  top: .1rem;
  height: .22rem;
  width: .22rem;
  background: url(/static/image/cart/add_cart_close@3x.png);
  background-size: 100% 100%;
}
.cart-list{
  padding-top: .15rem;
  min-height: .82rem;
  width: 100%;
  color: #333;
  border-bottom: .01rem solid #E5E5E5;
}
.cart-list:last-child{
  padding:0;
  line-height: .82rem;
  clear: both;
}
.cart-list .title{
  margin-bottom: .15rem;
}
.cart-add>footer{
  position: absolute;
  bottom: 0;
  left: -.1rem;
  right: 0;
  color: #FFFFFF;
  height: .5rem;
  line-height: .5rem;
  text-align: center;
    background: -webkit-linear-gradient(left, rgb(255, 161, 38), rgb(255, 139, 32));
    background: linear-gradient(to right, rgb(255, 161, 38), rgb(255, 139, 32));
}
.cart-list .conetnt span{
  font-size: .12rem;
  color: #333;
  line-height: 2.5;
  margin-right: .05rem;
  padding: .05rem .13rem;
  border-radius: .3rem;
  background-color: #F5F5F5;
}
.cart-list .conetnt span.selected{
  color: #FFFFFF;
    background: -webkit-linear-gradient(right, top, rgba(223, 10, 23,.8), rgba(255, 139, 32,.8));
    background: linear-gradient(to left top, rgba(223, 10, 23,.8), rgba(255, 139, 32,.8));
}
.cart-size-oper{
  float: right;
  height: 100%;
}
.cart-size-oper i {
  display: inline-block;
  width: .33rem;
  height: .33rem;
  vertical-align: middle;
}
.cart-size-oper i:first-child{
  margin-right: .05rem;
}
.cart-size-oper i.reset,.cart-size-oper i.shift{
  background:url(/static/image/cart/add_cart_number_cut_nor@3x.png) ;
  background-size: 100% 100%;
}
.cart-size-oper i.push{
  background:url(/static/image/cart/add_cart_number_add@3x.png) ;
  background-size: 100% 100%;
}
.cart-size-oper i.size{
  width: .38rem;
  height: .3rem;
  text-align: center;
  font-style: normal;
  line-height: .3rem;
  margin: 0 .02rem;
  background-color: #F5F5F5;
}
.cart-size-oper i.shift.no{
  background:url(/static/image/cart/add_cart_number_cut_dis@3x.png) ;
  background-size: 100% 100%;
}
.goods-detail .v-modal{
  z-index: 99!important;
}
.goods-detail .mint-popup{
  z-index: 100!important;
}
.toSelect span{
  margin-right: .05rem;
}
</style>


