<template>
  <div class="goods-detail">
    <header :class="{fixdStyle:isTop}">
      <span v-if="isTop">{{spObj.name}}</span>
      <i class="goods-back" @click="toback"></i>
      <span class="good-oper">
        <i class="goods-share"></i>
        <i class="goods-collection" @click="collection(spObj.skuId)"></i>
      </span>
    </header>
    <section v-if="isfixed" class="good-fixed-detail"><small><span class="good-price">￥{{spObj.sellPrice}}</span><span class="good-sales">已售{{spObj.sellAmount}}件</span></small></section>
    <section class="goods-detail-contanier" @touchmove="move" ref="aa">
      <div class="goods-detail-banner"><img :src="spObj.imgUrl"></div>
      <div class="goods-detail-mes">
        <div class="goods-name">{{spObj.name}}</div>
        <small><span class="good-price">￥{{spObj.sellPrice}}</span><span class="good-sales">已售{{spObj.sellAmount}}件</span></small>
      </div>
      <section class="store-mess">
        <header>
          <div class="store-title">{{spObj.storeName}}</div><small>{{spObj.summary}}</small></header>
        <div class="store-mess-content">
          <div class="address-icon"><i></i></div>
          <div class="address-text">
            <div class="store-address">{{spObj.address}}</div>
            <div class="store-distance">距您<span>{{spObj.distance}}</span></div>
          </div>
          <div class="telphone"><a :href="phone"><i></i></a></div>
        </div>
      </section>
      <div class="good-summary">
        <header><span>商品详情</span></header>
        <section class="good-summary-content" v-html="spObj.desc">
        </section>
      </div>
    </section>
    <footer>
      <div class="addCart" @click="pay(0)">加入购物车</div>
      <div class="goPay" @click="pay(1)">立即购买</div>
    </footer>
    <mt-popup v-model="popupVisible" position="bottom" class="sp-list">
      <select-view @closePop="closePop" :oper="type" :skuId="spObj.skuId" :spuId="spObj.spuId" :sellPrice="spObj.sellPrice" :sellAmount="spObj.sellAmount"></select-view>
    </mt-popup>
  </div>
</template>

<script>
  import select from '@/components/list/select'
  import {getProductBySkuId,addCollectgood} from '../../service/getData'
  export default {
    components: {
      'select-view': select
    },
    data () {
      return {
        top: 0,
        isTop: false,
        isfixed: false,
        popupVisible: false,
        type: 0,
        spObj: {},
        phone: ''
      }
    },
    created: function () {
      let _this = this
      let skuId,latitude,longitude
      skuId = this.$route.query.skuId
      latitude = this.$route.query.latitude
      longitude = this.$route.query.longitude
      getProductBySkuId(skuId, latitude, longitude).then(res => {
        _this.spObj = res.data
        if (res.data.phone !== '') {
          _this.phone = 'tel:' + res.data.phone
        } else {
          _this.phone = 'javascript:void(0)'
        }
      })
    },
    methods: {
      collection: function (skuId) {
        let _this = this
        addCollectgood(skuId)
        .then(res => {
          if (res.data.code === 1) {
            _this.$toast('收藏商品成功')
          }
        })
        .catch(res => {
          _this.$toast('收藏商品失败')
        })
      },
      move: function (e) {
        if (this.$refs.aa.scrollTop >= 119) {
          this.isTop = true
        } else {
          this.isTop = false
        }
        if (this.$refs.aa.scrollTop >= 159) {
          this.isfixed = true
        } else {
          this.isfixed = false
        }
      },
      closePop: function (val) {
        this.popupVisible = val
      },
      pay: function (val) {
        this.popupVisible = true
        this.type = val
      },
      toback: function () {
        this.$router.go(-1)
      }
    },
    mounted: function () {},
    watch: {
      top: function () {
        console.log(this.top)
      }
    }
  }
</script>

<style>
/* 商品详情页 */
.goods-detail{
  height: 100%;
  overflow: hidden;
}
.goods-detail>header{
  clear: both;
  height: .4rem;
  line-height: .3rem;
  text-align: center;
  width: 100%;
  position: fixed;
  z-index: 1;
  color: #FFFFFF;
  padding: .05rem  .15rem;
  background: rgba(0,0,0,0);
}
.goods-detail>header span.good-oper{
  float: right;
}
.goods-detail>header i{
  height: .3rem;
  width: .3rem;
  display: inline-block;
}
.goods-detail>header .goods-back{
  float: left;
  background: url(/static/image/classify/classify_back@3x.png);
    background-size: 100% 100%;
}
.goods-detail>header .goods-share{
  padding: 0;
    background: url(/static/image/classify/classify_share@3x.png);
    background-size: 100% 100%;
}
.goods-detail>header .goods-collection{
  background: url(/static/image/store/store_collect@3x.png);
    background-size: 100% 100%;
}
.goods-detail-contanier{
  height: 100%;
  overflow: auto;
  padding-bottom: .5rem;
  -webkit-overflow-scrolling: touch;
}
.goods-detail-banner{
  height: 1.59rem;
  background-color: gainsboro;
}
.goods-detail-mes{
  height: .83rem;
  padding: 0 .15rem;
    border-bottom: .05rem solid #f5f5f5;
}
.goods-detail-mes>small,.good-fixed-detail>small{
  color: #777777;
    display: block;
    height: .4rem;
    line-height: .4rem;
}
.goods-name{
  height: .4rem;
    color: #333;
    line-height: .4rem;
}
.good-price{
  color: rgb(255, 161, 38);
}
.good-sales{
  float: right;
}
.good-summary>header{
  height:.53rem;
  text-align: center;
  line-height: .53rem;
}
.good-summary>header>span{
  position: relative;
  padding: 0 .1rem;
}
.good-summary>header>span:before{
  position: absolute;
  content: '';
  top: 50%;
  left: -.05rem;
  transform: translateX(-100%);
  height: .02rem;
  width: .2rem;
  background-color: #E9E9E9;
}
.good-summary>header>span:after{
  position: absolute;
  content: '';
  top: 50%;
  right: -.05rem;
  transform: translateX(100%);
  height: .02rem;
  width: .2rem;
  background-color: #E9E9E9;
}
.good-summary-content{
  width: 100%;
  padding: .1rem;
}
.goods-detail>footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: .5rem;
  color: #FFFFFF;
  line-height: .5rem;
}
.goods-detail>footer div{
  float: left;
  width: 50%;
  text-align: center;
  height: 100%;
}
.addCart{
  background-color: #FFB23C;
}
.goPay{
  background-color: #FF7A00;
}
.goods-detail>header.fixdStyle{
  background: -webkit-linear-gradient(left, rgb(255, 161, 38), rgb(255, 139, 32));
    background: linear-gradient(to right, rgb(255, 161, 38), rgb(255, 139, 32));
}
.good-fixed-detail{
  height: .4rem;
  top: .4rem;
  width: 100%;
  z-index: 1;
  padding: 0 .15rem;
  position: fixed;
  background-color: rgb(255,255,255);
}
</style>


